<template>

	<div class="d-box v-box">
		<div class="headDiv" style="padding: 20px;">
			<div class="headerLeft">
				<span class="system">VIN</span>
				<textarea class="input" v-model="textarea" placeholder='多个VIN请以英文" , "分割'></textarea>
			</div>
			<div class="headerRight">
				<SolidButton :type='"solid"' :text='"查询"' @click="query"></SolidButton>
				<SolidButton :type='"hollow"' :text='"重置"' @click="reset"></SolidButton>
			</div>
		</div>
		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable spaceTable">
					<div class="tableDiv">
						<el-table v-loading="loading" ref="multipleTable" stripe :data="rows" tooltip-effect="dark" style="width: 100%;" height="100%">
							<el-table-column label="VIN">
								<template slot-scope="scope">
									<a id="vin" @click="details(scope.row)">{{scope.row|vin}}</a>
								</template>
							</el-table-column>
							<el-table-column label="车型大类">
								<template slot-scope="scope">
									<span>{{scope.row|carmodel}}</span><br />
								</template>
							</el-table-column>
							<el-table-column v-if='custType.custType == 2 || custType.custType == 1' prop="orderSrcWhName" label="所属主机厂">
							</el-table-column>
							<el-table-column v-if='custType.custType == 3 || custType.custType == 1' prop="customerName" label="所属经销商">
							</el-table-column>
							<el-table-column label="出发地">
								<template slot-scope="scope">
									<span>{{scope.row|setout}}</span><br />
								</template>
							</el-table-column>
							<el-table-column label="目的地">
								<template slot-scope="scope">
									<span>{{scope.row|Destination}}</span><br />
								</template>
							</el-table-column>
							<el-table-column prop="currentLocation" label="当前位置">
							</el-table-column>
							<!--<el-table-column label="省份" show-overflow-tooltip>
								<template slot-scope="scope">
									<span style="margin-left: 10px" v-for="o in scope.row.areaComRegionList">{{o.province}}&nbsp;&nbsp;</span><br />
								</template>
							</el-table-column>-->

						</el-table>
					</div>
				</div>
				<div class="footerDiv">
					<el-pagination style="text-align: center;padding: 6px 0px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage1" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="total, sizes, prev, pager, next, jumper" :total="num">
					</el-pagination>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import Table from '../../table/Table.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		filters: {
			vin(value) {
				return value.vin
			},
			carmodel(value) {
				return value.cateName + '' + value.modelName
			},
			setout(value) {
				return value.orderSrcPName + '' + value.orderSrcCName + '' + value.orderSrcWhName
			},
			Destination(value) {
				return value.orderDestPName + '' + value.orderDestCName + '' + value.orderDestWhName
			},
		},
		components: {
			Table,
			SolidButton
		},
		data() {
			return {
				vin: null,
				currentPage1: 1,
				pageNum: '1',
				pageRows: '10',
				name: '',
				rows: [],
				num: 0,
				lnglatXY: [],
				textarea: '',
				custType: '',
				loading: true
			}
		},
		methods: {

			//初始化数据
			loadData() {
				var self = this;
				var p = {
					pageNum: this.pageNum,
					pageRows: this.pageRows,
					vins: this.vin,
					operateCode: '103',
					operateName: 'test'
				}
				self.$api.post('crm/crmOrder/getOrderListByVin', p, r => {
					self.rows = r.repData.list
					self.num = r.repData.total
					self.loading = false
				}, e => {
					self.loading = false
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})

			},
			//			regeocoder() { //逆地理编码
			//				var map = new AMap.Map("container", {
			//						resizeEnable: true,
			//						zoom: 18
			//					}),
			//					lnglatXY = [121.5224800000, 31.2667900000]; //已知点坐标
			//
			//				var geocoder = new AMap.Geocoder({
			//					radius: 1000,
			//					extensions: "all"
			//				});
			//				geocoder.getAddress(lnglatXY, function(status, result) {
			//					if(status === 'complete' && result.info === 'OK') {
			//						geocoder_CallBack(result);
			//					}
			//				});
			//
			//				function geocoder_CallBack(data) {
			//					var address = data.regeocode.formattedAddress; //返回地址描述
			//					document.getElementById("result").innerHTML = address;
			//					console.log(address)
			//				}
			//},
			//vin详情
			details(row) {
				//				console.log(row)
				this.$router.push({
					path: "/OrderDetails",
					query: {
						vin: row.vin
					}
				})
			},
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			},
			getSplitString(str) {
				var arr = str.split(",");
				var resources = "";
				for(var i = 0; i < arr.length; i++) {
					var arr1 = arr[i].split(/\s+/);
					for(var j = 0; j < arr1.length; j++) {
						if(jQuery.trim(arr1[j]) != "") {
							resources += jQuery.trim(arr1[j]) + ",";
						}
					}
				}
				return resources;
			},
			//查询
			query() {
				if(this.textarea == '') {
					this.vin = null
				} else {
					this.vin = []
					this.vin = this.getSplitString(this.textarea).split(",")
					var reg = /^[0-9a-zA-Z]+$/
					for(var i = 0; i < this.vin.length; i++) {
						for(var k = 0; k < this.vin[i].length; k++) {
							if(!reg.test(this.vin[i][k])) {
								this.$message({
									message: '只能输入数字或字符',
									type: 'warning'
								});
								return false;
							}
						}
					}
				}
				this.loadData();
			},
			//重置
			reset() {
				this.vin = null;
				this.textarea = '';
				this.loadData();
			}
		},
		created() {
			this.loadData();
			this.custType = this.$api.sGetObject("_user");
		},
	}
</script>

<style scoped="scoped">
	header {
		padding: 20px;
		background: #fff;
		overflow: hidden;
	}
	
	.headerLeft {
		width: 65%;
		height: 100px;
		float: left;
		margin-right: 40px;
	}
	
	.headerLeft .el-input {
		width: 188px;
		height: 37px;
	}
	
	.system {
		display: inherit;
		line-height: 40px;
		width: 5%;
		float: left;
	}
	
	.headerRight {
		width: 20%;
		float: right;
	}
	
	.style1 {
		background: #D9524F;
		color: #fff;
	}
	
	.style2 {
		border-color: #D9524F;
		color: #D9524F;
	}
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		/*padding: 20px;*/
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
	
	#vin {
		color: #71A8D6;
		cursor: pointer;
	}
	
	.input {
		width: 92%;
		height: 70%;
		float: right;
		border: 1px solid #F3F3F3;
		resize: none;
		padding: 10px;
	}
</style>


